// Document Sign API v1
// POST /api/v1/revenue-engine/documents/[id]/sign - Submit signature for document

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

type Props = {
  params: Promise<{ id: string }>
}

// POST - Submit signature
export async function POST(request: NextRequest, props: Props) {
  try {
    const { id } = await props.params
    const body = await request.json()
    const {
      accessToken,
      signatureData, // Base64 signature image or typed signature
      signatureType, // 'draw', 'type', 'upload'
      fieldValues, // Array of { fieldId, value }
      ipAddress,
      userAgent,
    } = body
    
    if (!accessToken) {
      return NextResponse.json(
        { success: false, error: 'Access token is required' },
        { status: 401 }
      )
    }
    
    if (!signatureData) {
      return NextResponse.json(
        { success: false, error: 'Signature data is required' },
        { status: 400 }
      )
    }
    
    // Fetch document with signers
    const { data: document, error: fetchError } = await supabaseAdmin
      .from('Document')
      .select(`
        *,
        signers:DocumentSigner (*)
      `)
      .eq('id', id)
      .single()
    
    if (fetchError || !document) {
      return NextResponse.json(
        { success: false, error: 'Document not found' },
        { status: 404 }
      )
    }
    
    // Validate access token
    const signer = document.signers?.find((s: any) => s.accessToken === accessToken)
    if (!signer) {
      return NextResponse.json(
        { success: false, error: 'Invalid access token' },
        { status: 403 }
      )
    }
    
    // Check if signer already signed
    if (signer.status === 'signed') {
      return NextResponse.json(
        { success: false, error: 'You have already signed this document' },
        { status: 400 }
      )
    }
    
    // Check document status
    if (!['sent', 'viewed', 'partially_signed'].includes(document.status)) {
      return NextResponse.json(
        { success: false, error: 'Document is not available for signing' },
        { status: 400 }
      )
    }
    
    // Check expiration
    if (document.expiresAt && new Date(document.expiresAt) < new Date()) {
      return NextResponse.json(
        { success: false, error: 'This document has expired' },
        { status: 400 }
      )
    }
    
    // Check ID verification requirements
    if (signer.requireIdVerification) {
      const { data: verification } = await supabaseAdmin
        .from('SignerIdVerification')
        .select('status')
        .eq('signerId', signer.id)
        .eq('status', 'verified')
        .single()
      
      if (!verification) {
        return NextResponse.json(
          { success: false, error: 'ID verification is required before signing' },
          { status: 400 }
        )
      }
    }
    
    // Update signer with signature
    const clientIp = ipAddress || request.headers.get('x-forwarded-for') || request.headers.get('x-real-ip')
    const clientUserAgent = userAgent || request.headers.get('user-agent')
    
    const { error: updateSignerError } = await supabaseAdmin
      .from('DocumentSigner')
      .update({
        status: 'signed',
        signedAt: new Date().toISOString(),
        signatureData,
        signatureType: signatureType || 'draw',
        signatureIpAddress: clientIp,
        signatureUserAgent: clientUserAgent,
      })
      .eq('id', signer.id)
    
    if (updateSignerError) throw updateSignerError
    
    // Save field values if provided
    if (fieldValues && fieldValues.length > 0) {
      const fieldValueRecords = fieldValues.map((fv: any) => ({
        documentId: id,
        fieldId: fv.fieldId,
        signerId: signer.id,
        value: fv.value,
      }))
      
      await supabaseAdmin
        .from('DocumentFieldValue')
        .insert(fieldValueRecords)
    }
    
    // Log activity
    await supabaseAdmin
      .from('DocumentActivity')
      .insert({
        documentId: id,
        signerId: signer.id,
        action: 'signed',
        description: `${signer.name} signed the document`,
        ipAddress: clientIp,
        userAgent: clientUserAgent,
      })
    
    // Check if all signers have signed
    const { data: allSigners } = await supabaseAdmin
      .from('DocumentSigner')
      .select('status')
      .eq('documentId', id)
    
    const pendingSigners = allSigners?.filter((s: any) => s.status !== 'signed') || []
    const allSigned = pendingSigners.length === 0
    
    // Update document status
    const newStatus = allSigned ? 'completed' : 'partially_signed'
    await supabaseAdmin
      .from('Document')
      .update({
        status: newStatus,
        completedAt: allSigned ? new Date().toISOString() : null,
        updatedAt: new Date().toISOString(),
      })
      .eq('id', id)
    
    // Log completion if all signed
    if (allSigned) {
      await supabaseAdmin
        .from('DocumentActivity')
        .insert({
          documentId: id,
          action: 'completed',
          description: 'All parties have signed the document',
        })
    }
    
    return NextResponse.json({
      success: true,
      data: {
        message: allSigned 
          ? 'Document has been fully signed by all parties' 
          : 'Your signature has been recorded. Waiting for other signers.',
        documentStatus: newStatus,
        allSigned,
        pendingSigners: pendingSigners.length,
      },
    })
  } catch (error: any) {
    console.error(`POST /api/v1/revenue-engine/documents/${(await props.params).id}/sign error:`, error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to sign document' },
      { status: 500 }
    )
  }
}
